<template>
    <div>
      <div class="row">
        <div class="col-2">
          <h3 class="fw-bold">Resellers</h3>
        </div>
        <div class="col">
          <v-btn tile color="error" @click="exportData" large>Export Excel</v-btn>
        </div>
        <div class="col-4">
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" placeholder="Search" single-line hide-details size="lg"></v-text-field>
        </div>
        <div class="col">
          <router-link to="/admin/resellers/new">
            <v-btn tile color="error" large><v-icon left>mdi-plus</v-icon>CREATE RESELLERS</v-btn>
          </router-link>
        </div>
      </div>
      <v-row class="mt-5">
        <v-col class="d-flex" cols="4" sm="4">
          <v-autocomplete
              v-model="state_id"
              @change="getSelectedData"
              :items="allstates"
              item-text="state"
              item-value="id"
              label="State" dense>
          </v-autocomplete>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-autocomplete
              v-model="city_id"
              @change="getSelectedData"
              :items="cities"
              item-text="city"
              item-value="id"
              label="City" dense>
          </v-autocomplete>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-autocomplete
              v-model="status"
              @change="getSelectedData"
              :items="Action_status"
              item-text="text"
              item-value="val"
              label="Active/Inactive" dense>
          </v-autocomplete>
        </v-col>
      </v-row>
      <div class="">
        <v-data-table :search="search" :headers="headers" :items="resellers" ref="myDataTable" sort-by="calories" :single-select="singleSelect" class="elevation-1">

          <template v-slot:[`item.id`]="{ item }">
            <div>RES{{ item.id }}</div>
          </template>

          <template v-slot:top>
            
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </template>

          <template v-slot:[`item.avatar`]="{ item }">
            <div v-if="item.user_avatar!=null">
              <v-img :src=(url+item.user_avatar) size="40"></v-img>
            </div>
            <div v-else>
              <v-avatar color="primary" size="40">
                <span class="white--text text">{{ item.name.toUpperCase().split(' ', 2).map(word => word.charAt(0)).join('') }}</span>
              </v-avatar>
            </div>
          </template>

          <template v-slot:[`item.reseller_skills`]="{ item }">
            <div v-for="(skills, i) in item.allskill" :key="i">
              <span v-if="item.allskill.length>0">{{ skills.skill }},</span>
            </div>
          </template>

          <template v-slot:[`item.active`]="{ item }">
          <div v-if="item.user_status === '1'">
            <!-- <v-icon small class="mr-2">mdi-check</v-icon> -->Active</div>
          <div v-else>
            <!-- <v-icon small class="mr-2">mdi-close</v-icon> -->Inactive</div>
        </template>

          <template v-slot:[`item.action`]="{ item }">
            <router-link :to="{name:'resellersupdate', params:{id:item.id}}" class="mtitle text-decoration-none link-dark h4">
                <v-icon small title="Edit">mdi-pencil</v-icon>
            </router-link>
            <v-icon small  @click="deleteItem(item)" title="Delete"> mdi-delete</v-icon>
            <!-- <v-icon small @click="login(item)"> mdi-logout</v-icon> -->
          </template>
          
        </v-data-table>
        
      </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: "ResellersView",
    components: {  },
    data: () => ({
        url:'https://bizlx.itechvision.in',
        search: '',
        allstates: [],
        cities: [],
        addedSkills: '',
        Action_status: [{val:1, text:'Active'}, {val:0, text:'Inactive'}],
        dialog: false,
        singleSelect: false,
        dialogDelete: false,
        headers: [
            { text: "Avatar", value: "avatar" },
            { text: "Name", align: "start", sortable: false, value: "name",},
            { text: "E-mail", value: "email" },
            { text: "Mobile", value: "mobile_phone" },
            { text: "City", value: "city" },
            { text: "State", value: "state" },
            { text: "Reseller Id", value: "id" },
            { text: "Reseller Skill's", value: "reseller_skills" },
            { text: "Status", value: "active", sortable: false },
            { text: "Actions", value: "action", sortable: false },
        ],
        resellers: [],
        editedIndex: -1,
        editedItem: {
            id: '',
            name: "",
            email: "",
            mobile_phone: "",
            city: "",
            state: "",
            reseller_id: "",
            reseller_skills: "",
        },
        defaultItem: {
            id: '',
            name: "",
            email: "",
            mobile_phone: "",
            city: "",
            state: "",
            reseller_id: "",
            reseller_skills: "",
        },
    }),


    watch: {
        dialog(val) {
            val || this.close();
        },
        dialogDelete(val) {
            val || this.closeDelete();
        },
    },
    created() {
        this.All_States();
        this.getResellers();
    },
    methods: {
      getResellers(){
          axios.get('resellers')
          .then((resp)=>{
            this.resellers = resp.data.resellers;
          });
      },
      All_States(){
        axios.get('allstates')
          .then((resp)=>{
            this.allstates = resp.data.locations;
          });
      },
      getSelectedData() {
          if (this.state_id) {
            axios.get('states/'+this.state_id)
              .then((resp)=>{
                this.cities = resp.data;
              });
          }
        // Search muiltiple
        var searchData = {
          state_id : this.state_id,
          city_id : this.city_id,
          status : this.status,
        };
        axios.post('admin/reseller/search', searchData )
        .then((resp)=>{
          this.resellers = resp.data.resellers;
        });
      },
        deleteItem(item) {
            this.editedIndex = this.resellers.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialogDelete = true;
        },
        deleteItemConfirm() {
            this.resellers.splice(this.editedIndex, 1);
            this.closeDelete();
        },
        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },
        closeDelete() {
            this.dialogDelete = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },
        exportData() {
          // Export data table
          const data = this.$refs.myDataTable.items;
          const exportedData = [];
          data.forEach((item) => {
            // added skills
            this.addedSkills = '';
            for(var i = 0; i < item.allskill.length; i++){
                this.addedSkills += item.allskill[i].skill+' | ';
            }
            const exportedItem = {
              id: item.id,
              ResellersName: item.name,
              Email: item.email,
              Mobile: item.mobile_phone,
              City: item.city,
              State: item.state,
              ResellerId: "RES"+item.id,
              ResellerSkill: this.addedSkills,
              Created: item.added_date,
              // Add more properties as needed
            };
            exportedData.push(exportedItem);
          });
          console.warn(exportedData);
          const csvContent = this.convertToCSV(exportedData);
          const link = document.createElement('a');
          link.setAttribute('href', 'data:text/csv;charset=utf-8,' + encodeURIComponent(csvContent));
          link.setAttribute('download', 'reseller_exported_data.csv');
          link.style.display = 'none';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        },
        convertToCSV(data) {
          // Convert the data array to a CSV string
          const header = Object.keys(data[0]).join(',') + '\n';
          const rows = data.map((item) => Object.values(item).join(',')).join('\n');
          return header + rows;
        },
    },
}
</script>